import React, { useRef, useState } from 'react';
import MyButton from '../UI/MyButton';
import styles from './buisnesform.module.css';
import { useDispatch, useSelector } from 'react-redux';
import { addDeal } from '../../store/store';
import { nanoid } from 'nanoid';
import BuisnesItem from './BuisnesItem/BuisnesItem';
import BuisnessTime from './BuisnessTime/BuisnessTime';

const BuisnesForm = () => {
  const [value, setValue] = useState('');
  const inputRef = useRef(null);
  const dispatch = useDispatch();
  const dealList = useSelector(state => state.deals);
  const dealTime = dealList.reduce((prev, curr) => prev + curr.count, 0) * 25;

  const handleChange = (e) => {
    setValue(e.target.value.toLowerCase());
  }

  const handleSubmit = () => {
    dispatch(addDeal({text: value}))
    inputRef.current.value = '';
  }

  return (
    <div className={styles.container}>
      <input
        ref={inputRef}
        className={styles.buisnessName}
        type="text"
        name="buisnessName"
        id="buisnessName"
        placeholder='Название дела'
        onChange={handleChange}
      />
      <MyButton
        color={'var(--white)'}
        padding={'20px 50px'}
        bgcolor={'var(--green)'}
        mWidth={'175px'}
        onClick={handleSubmit}
        type={'submit'}
      >Добавить</MyButton>
      <ul>
        {dealList.length > 0 && (
          dealList.map((deal, index) =>
            <BuisnesItem key={nanoid()} text={deal.text} index={index} count={deal.count} />
          )
        )}
      </ul>
      <BuisnessTime time={dealTime} />
    </div>
  );
};

export default BuisnesForm;
